import React from 'react'
import styles from './AboutUs.module.css'
import Circle from '../../images/Circle.svg'

export default function AboutUs() {
    return (
        <div className={styles.aboutUs}>
            <div className={styles.aboutUsTextWrapper}>
                <div className={styles.aboutUsTitle}>
                    <h1>Почему выбирают нас?</h1>
                </div>
                <div className={styles.aboutUsInfo}>
                    <div className={styles.aboutUsNumber}>
                        <p>01</p>
                    </div>
                    <div className={styles.aboutUsPoint}>
                        <div className={styles.aboutUsInfoTitle}>
                            <h2>Находим</h2>
                        </div>
                        <div className={styles.aboutUsInfoText}>
                            <p>поиск производителя если товар <br />отсутствует у наших поставщиков</p>
                        </div>
                    </div>
                </div>
                <div className={styles.aboutUsInfo}>
                    <div className={styles.aboutUsNumber}>
                        <p>02</p>
                    </div>
                    <div className={styles.aboutUsPoint}>
                        <div className={styles.aboutUsInfoTitle}>
                            <h2>Оформляем</h2>
                        </div>
                        <div className={styles.aboutUsInfoText}>
                            <p>готовим документы и проводим валютные <br />платежи (работаем с НДС и без НДС).</p>
                        </div>
                    </div>
                </div>
                <div className={styles.aboutUsInfo}>
                    <div className={styles.aboutUsNumber}>
                        <p>03</p>
                    </div>
                    <div className={styles.aboutUsPoint}>
                        <div className={styles.aboutUsInfoTitle}>
                            <h2>Доставляем</h2>
                        </div>
                        <div className={styles.aboutUsInfoText}>
                            <p>проводим самостоятельно все необходимые<br /> таможенные операции. доставка по всей России</p>
                        </div>
                    </div>
                </div>
            </div>
            <div className={styles.aboutUsCircles}>
                <div className={styles.aboutUsCircle}>
                    <img src={Circle} alt='TechnicLogo'></img>
                </div>
                <div className={styles.aboutUsCircle}>
                    <img src={Circle} alt='TechnicLogo'></img>
                </div>
            </div>
        </div >
    )
}
