import React from 'react'
import styles from './CarMarks.module.css'
import CNHTC from '../../images/CarBrand/CNHTC.svg'
import DFM from '../../images/CarBrand/DFM.svg'
import FAW from '../../images/CarBrand/FAW.svg'
import FOTON from '../../images/CarBrand/FOTON.svg'
import SHACMAN from '../../images/CarBrand/SHACMAN.svg'
import SITRAK from '../../images/CarBrand/SITRAK.svg'

export default function CarMarks() {
    return (
        <div className={styles.carBrandsLine}>
            <div className={styles.carBrand}>
                <img src={CNHTC} alt='CNHTC'></img>
            </div>
            <div className={styles.carBrand}>
                <img src={DFM} alt='DFM'></img>
            </div>
            <div className={styles.carBrand}>
                <img src={FAW} alt='FAW'></img>
            </div>
            <div className={styles.carBrand}>
                <img src={FOTON} alt='FOTON'></img>
            </div>
            <div className={styles.carBrand}>
                <img src={SHACMAN} alt='SHACMAN'></img>
            </div>
            <div className={styles.carBrand}>
                <img src={SITRAK} alt='SITRAK'></img>
            </div>
        </div>
    )
}
