import React, { useState } from 'react'
import styles from './MainInfo.module.css'
// import FirstPictBig from '../../images/MainInfo/FirstPictBig.png'
// import SecondPictBig from '../../images/MainInfo/SecondPictBig.png'
// import ThirdPictBig from '../../images/MainInfo/ThirdPictBig.png'
// import FirstPictSmall from '../../images/MainInfo/FirstPictSmall.png'
// import SecondPictSmall from '../../images/MainInfo/SecondPictSmall.png'
// import ThirdPictSmall from '../../images/MainInfo/ThirdPictSmall.png'
import FirstPic from '../../images/MainInfo/FirstPic.jpg'
import SecondPic from '../../images/MainInfo/SecondPic.png'
import ThirdPic from '../../images/MainInfo/ThirdPic.png'

export default function MainInfo() {
    const [widths, setWidths] = useState({
        first: '66%',
        second: '17%',
        third: '17%'
    });
    const handleHover = (element) => {
        if (element === 'first') {
            setWidths({
                first: '66%',
                second: '17%',
                third: '17%'
            });
        } else if (element === 'second') {
            setWidths({
                first: '17%',
                second: '66%',
                third: '17%'
            });
        } else if (element === 'third') {
            setWidths({
                first: '17%',
                second: '17%',
                third: '66%'
            });
        }
    };
    return (
        <div className={styles.mainInfo}>
            <div className={styles.mainInfoPicts}>
                <div className={styles.mainInfoPictOne} id={styles.mainInfoPictOne} style={{ width: widths.first }}
                    onMouseEnter={() => handleHover('first')}
                    onMouseLeave={() => setWidths({ first: '66%', second: '17%', third: '17%' })}>
                    <img src={FirstPic} alt='FirstPict'></img>
                </div>
                <div className={styles.mainInfoPict} id={styles.mainInfoPictTwo} style={{ width: widths.second }}
                    onMouseEnter={() => handleHover('second')}
                    onMouseLeave={() => setWidths({ first: '66%', second: '17%', third: '17%' })}>
                    <img src={SecondPic} alt='FirstPict'></img>
                </div>
                <div className={styles.mainInfoPict} id={styles.mainInfoPictThree} style={{ width: widths.third }}
                    onMouseEnter={() => handleHover('third')}
                    onMouseLeave={() => setWidths({ first: '66%', second: '17%', third: '17%' })}>
                    <img src={ThirdPic} alt='FirstPict'></img>
                </div>
                <div className={styles.mainInfoTextBlock}>
                    <div className={styles.mainInfoTextWrapper}>
                        <div className={styles.mainInfoText}>
                            <h2>
                                Поставщики техники для грузовых <br />автомобилей Турции и Китая
                            </h2>
                            <h1 className={styles.mainInfoMainText}>
                                <span className={styles.headText}>комплексный</span> подход <br /> к решению вашей задачи
                            </h1>
                        </div>
                        <button className={styles.mainInfoButton}>Заказать технику</button>
                    </div>
                </div>
            </div>
        </div >
    )
}